}
@media (min-width: 1440px) {
  .additional-products__title {
    position: relative;
    display: inline-block;
    padding: 0;
  }
  .additional-products__title::before {
    content: "";
    position: absolute;
    left: -100vw;
    top: 23px;
    right: calc(100% + 40px);
    bottom: calc(100% - 25px);
    background-color: #68b738;
  }
  .additional-products__title::after {
    content: "";
    position: absolute;
    left: calc(100% + 35px);
    top: 23px;
    right: -100vw;
    bottom: calc(100% - 25px);
    background-color: #68b738;
  }
}

