.button {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 10px 10px;
  font-size: 16px;
  font-weight: 400;
  font-family: inherit;
  line-height: 20px;
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
.button:focus {
  outline-width: 2px;
  outline-style: solid;
  outline-offset: 1px;
}
.button:disabled {
  opacity: 0.5;
}
.button_green {
  background-color: #68b738;
}
.button_green:hover, .button_green:active {
  background-color: #5eaa2f;
}
.button_green:active {
  color: #ffffff;
}
.button_green:focus {
  outline-color: #5eaa2f;
}

.button__line {
  display: block;
  width: 100%;
  background-color: #000000;
  border-top: 1px solid #000000;
  border-bottom: 1px solid #000000;
}
.button__line::before {
  content: "";
  background-color: #000000;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 100%;
  background-repeat: no-repeat;
  background-size: contain;
  border: 1px solid #000000;
}
.button__line::after {
  content: "";
  background-color: #000000;
  position: absolute;
  left: 0;
  top: calc(100% - 2px);
  right: 0;
  bottom: 0;
  background-repeat: no-repeat;
  background-size: contain;
  border: 1px solid #000000;
}

