.program-version {
  margin: 0;
  padding: 0;

  @media (min-width: 1440px) {
    display: flex;
    justify-content: space-between;
  }
}

.program-version__item {
  background-color: $Concrete;
  list-style-type: none;
  margin-bottom: 20px;
  padding: 30px 20px 0;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 768px) {
    margin-bottom: 31px;
    padding: 77px 53px 53px;
  }

  @media (min-width: 1440px) {
    width: 466px;
    margin: 82px 0 0;
    padding: 77px 52px 59px;
  }
}

.program-version__title {
  position: relative;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: $Black;
  margin: 0 0 29px;
  padding-left: 70px;

  @media (min-width: 768px) {
    padding-left: 0;
    font-size: 36px;
    line-height: 36px;
    margin: 0 0 25px;
  }

  @media (min-width: 1440px) {
    margin: 0 0 73px;
    padding-left: 162px;
  }

  &::before {
    content: "";
    background-image: url("../img/cat_back-mobile.svg");
    background-position: center;
    background-size: contain;
    position: absolute;
    left: 0;
    top: 47%;
    background-repeat: no-repeat;
    width: 54px;
    height: 54px;
    transform: translateY(-50%);

    @media (min-width: 768px) {
      background-image: url("../img/cat_back-tablet.svg");
      left: auto;
      top: 3px;
      right: 109px;
      width: 200px;
      height: 212px;
      transform: translate(50%, 0%);
    }

    @media (min-width: 1440px) {
      background-image: url("../img/cat_back-tablet.svg");
      top: 50%;
      left: 0;
      width: 100px;
      height: 106px;
      transform: translateY(-50%);
    }
  }

  &_walking-cat {
    padding-left: 70px;

    @media (min-width: 768px) {
      padding-left: 0;
    }

    @media (min-width: 1440px) {
      padding-left: 162px;
    }

    &::before {
      left: -7px;
      top: 52%;
      background-image: url("../img/walking-cat_back-mobile.svg");
      width: 67px;
      height: 67px;

      @media (min-width: 768px) {
        background-image: url("../img/walking-cat_back-tablet.svg");
        top: 9%;
        left: 225px;
        width: 268px;
        height: 222px;
      }

      @media (min-width: 1440px) {
        background-image: url("../img/walking-cat_back-desctop.svg");
        top: 56%;
        left: -18px;
        width: 134px;
        transform: translateY(-50%);
      }
    }
  }
}

.program-version__text {
  font-family: $Arial;
  margin-bottom: 22px;

  @media (min-width: 768px) {
    width: 57%;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 48px;
  }

  @media (min-width: 1440px) {
    width: 100%;
    margin-bottom: 23px;
  }
}

.program-version__link {
  display: block;
  position: relative;
  padding: 20px 0;
  border-top: 1px solid $Alto;
  font-size: 16px;
  line-height: 16px;
  text-decoration: none;
  text-transform: uppercase;
  color: $Black;
  transition-property: opacity;
  transition-duration: 0.2s;

  &:active {
    opacity: 0.3;
  }

  &::after {
    content: "";
    background-image: url("../img/arrow.svg");
    background-position: 50%;
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    left: 101px;
    top: -2px;
    right: 0;
    bottom: 0;
    width: 33px;

    @media (min-width: 768px) {
      left: 132px;
      width: 32px;
    }

    @media (min-width: 1440px) {
      top: 1px;
      left: 136px;
      right: 0;
      width: 32px;
    }
  }

  &_pro {
    &::after {
      top: 0;
      left: 99px;

      @media (min-width: 768px) {
        left: 128px;
      }

      @media (min-width: 1440px) {
        left: 130px;
      }

    }
  }

  @media (min-width: 768px) {
    width: 25%;
    border: none;
    font-size: 20px;
    line-height: 30px;
    padding: 3px 0;
  }

  @media (min-width: 1440px) {
    width: 165px;
  }
}
