.additional-products {
  @media (min-width: 1440px) {
    grid-column: 1/2;
    grid-row: -1/-2;
  }
}

.additional-products__wrapper {
  padding: 50px 0 0;

  @media (min-width: 768px) {
    padding: 43px 0 0;
  }

  @media (min-width: 1440px) {
    padding: 50px 0 0;
  }
}

.additional-products__title {
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  color: $Black;
  margin: 0 0 35px;

  @media (min-width: 768px) {
    margin: 0 0 80px;
    padding: 0 37px;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: 1440px) {
    position: relative;
    display: inline-block;
    padding: 0;

    &::before {
      content: "";
      position: absolute;
      left: -100vw;
      top: 23px;
      right: calc(100% + 40px);
      bottom: calc(100% - 25px);
      background-color: $Apple;
    }

    &::after {
      content: "";
      position: absolute;
      left: calc(100% + 35px);
      top: 23px;
      right: -100vw;
      bottom: calc(100% - 25px);
      background-color: $Apple;
    }
  }
}

.additional-products__list {
  padding: 0;
  margin: 0 0 35px;

  @media (min-width: 768px) {
    margin: 0 0 69px;
    border: 1px solid $Silver;
  }

  @media (min-width: 768px) {
    margin: 0 0 69px;
    border-left: none;
    border-right: none;
  }
}

.additional-products__item {
  padding: 13px 0 18px;
  border-top: 1px solid $Silver;

  @media (min-width: 768px) {
    padding: 25px 37px 28px;
    border-top: none;
    border-bottom: 1px solid $Silver;
  }

  &:last-child {
    border-bottom: 1px solid $Silver;

    @media (min-width: 768px) {
      border-bottom: none;
    }
  }

  @media (min-width: 1440px) {
    padding: 16px 0 15px;
  }
}
